<template>
    <div class="page persondata">
        <Icon type="ios-arrow-back" class="goBack" @click="goBack()"/>
        <div class="con">
            <div class="con-box">
                <span>昵称：</span><input type="text" :value="personData.name">
            </div>
            <div class="con-box">
               <span>E-mail：</span><input type="text" value="请填写您的安全邮箱">
            </div>
            <div class="con-box">
               <span>性别：</span>
               <input type="radio" name="sex">男
               <input type="radio" name="sex">女
               <input type="radio" name="sex" checked>保密
            </div>
            <div class="con-box">
                <span>生日：</span><input type="date">
            </div>
            <div class="con-box">
                <span>身份证号：</span><input type="num" value="请输入您的18位身份证号">
            </div>
            <div class="con-des">
                <span>个人描述：</span>
                <textarea  cols="30" rows="10">这个人很懒，什么也没说</textarea>
            </div>
            <button type="button" class="modify">确认修改</button>
        </div>
    </div>
</template>
<script>
export default {
    name:'persondata',
    data(){
        return{
            personData:''
        }
    },
    created(){
        let data = JSON.parse(sessionStorage.booksUser);
        let isLoginData = JSON.parse(sessionStorage.isLogin);
        for(let i = 0; i < data.length; i++){
            if(data[i].name == isLoginData){
                this.personData = data[i];
            }
        }
    },
    mounted(){
        $('.tabBar').css('display','none');
    },
    methods:{
        goBack(){
            this.$router.go(-1);
            $('.tabBar').css('display','');
        }
    }
}
</script>
<style scoped>
.header{
    color:#fff;
    font-size: 0;
    height: 64px;
    line-height: 64px;
    position: relative;
}
.goBack{
    position: absolute;
    left:10px;
    top:20px;
    color:#fff;
    font-size: 20px;
}
.header .back{
    position: absolute;
    left:20px;
    top:50%;
    transform: translateY(-50%);
    font-size: 26px;
}
.header .title{
    width:100%;
}
.persondata{
    width:100%;
    
    background-color: #eee;
    height:calc(100% - 64px);
}
.con{
    height:calc(100% - 64px);
    width:100%;
}
.con-box{
    text-align: left;
    width:100%;
    height: 50px;
    border-bottom:1px solid #ccc;
    line-height: 50px;
    background-color: #fff;
}
.con-box input{
    letter-spacing: 2px;
    border:none;
    outline: none;
}
.con-box input[type='text'],.con-box input[type='num']{
    margin-left: 10px;
    width:calc(100% - 100px);
    height: 99%;
}
.con-box input[type='radio']{
    width:15px;
    height: 15px;
    vertical-align: middle;
    margin-left: 10px;
}
.con-box input[type='date']{
     margin-left: 10px;
    border:1px solid #ccc;
    line-height: 20px;
}
.con-box span{
    display: inline-block;
    width:60px;
    margin-left: 20px;
    color:#888;
}
.con-des{
    padding:15px 0;
    width:100%;
    text-align: left;
    border-bottom:1px solid #ccc;
    background-color: #fff;
}
.con-des span{
    vertical-align: top;
    display: inline-block;
    width:60px;
    margin-left: 20px;
    color:#888;
}
.con-des textarea{
    margin-left: 7px;
    width:calc(100% - 100px);
    height: 100px;
    border:none;
    outline: none;
}
.modify{
    margin: 30px 0;
    width:calc(100% - 20px);
    height: 50px;
    line-height: 50px;
    background-color: rgb(11, 102, 11);
    color:#fff;
    border:none;
    outline: none;
    border-radius: 5px;
    font-size: 16px;
}
</style>




