// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import $ from 'jquery'


Vue.config.productionTip = false

router.beforeEach((to, from, next) => {
  switch(to.name) {
    case "home":  store.commit('changeTitle', "主页"); break;
    case "classify":  store.commit('changeTitle', "分类"); break;
    case "shopping":  store.commit('changeTitle', "购物"); break;
    case "mine":  store.commit('changeTitle', "我的"); break;
    case "persondata": store.commit('changeTitle', "个人资料修改"); break;
  }
  next();
})

// 导入axios
import Axios from 'axios'
// 将axios挂载到Vue上
Vue.prototype.$axios = Axios
Axios.defaults.baseURL = './static/json/';

import iView from 'iview';
import 'iview/dist/styles/iview.css';
Vue.use(iView);

//引入bmob js-sdk
import Bmob from "hydrogen-js-sdk"; 
Bmob.initialize("0c211ce431e5e6d65e5c73fcb414b4b5", "6e455562d3e8407c32498e915b8a1511");
Vue.prototype.Bmob = Bmob
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



